.balloon {
  position: absolute;
  cursor: pointer;
  transition: opacity 0.5s ease-out, transform 0.5s ease-out;
}

.balloon.popped {
  opacity: 0;
  transform: scale(1.5);
}

#score {
  position: fixed;
  top: 10px;
  left: 10px;
  font-size: 24px;
  color: #ff69b4;
  z-index: 1000;
}
